import * as THREE from 'three'
import * as dat from 'dat.gui'
window.THREE = THREE
require('./OrbitControls')

export default class EthLogo {
  constructor (canvas, showControls = false) {
    this.canvas = canvas

    this.init()

    this.gui = new dat.GUI()
    let camFolder = this.gui.addFolder('Camera')
    camFolder.add(this.camera.position, 'z', -500, 500).listen().name('Camera Z')
    camFolder.add(this.camera.position, 'x', -500, 500).listen().name('Camera X')
    camFolder.add(this.camera.position, 'y', -500, 500).listen().name('Camera Y')
    camFolder.add(this.camera, 'fov', 0, 100).name('FOV').onChange(() => { this.camera.updateProjectionMatrix() })
    camFolder.add(this.camera, 'near', 0, 100).name('Near').onChange(() => { this.camera.updateProjectionMatrix() })
    camFolder.add(this.camera, 'far', 0, 2000).name('Far').onChange(() => { this.camera.updateProjectionMatrix() })

    let coneFolder = this.gui.addFolder('Logo')
    coneFolder.add(this.ethBottom.position, 'x', -50, 50).listen().name('BottomPosition X')
    coneFolder.add(this.ethBottom.position, 'y', -50, 50).listen().name('BottomPosition Y')
    coneFolder.add(this.ethBottom.position, 'z', -50, 50).listen().name('BottomPosition Z')
    coneFolder.add(this.ethBottom.rotation, 'x', -50, 50).listen().name('BottomRotation X')
    coneFolder.add(this.ethBottom.rotation, 'y', -50, 50).listen().name('BottomRotation Y')
    coneFolder.add(this.ethBottom.rotation, 'z', -50, 50).listen().name('BottomRotation Z')

    let lightFolder = this.gui.addFolder('Light')
    lightFolder.add(this.light.position, 'x', -50, 50).listen().name('X')
    lightFolder.add(this.light.position, 'y', -50, 50).listen().name('Y')
    lightFolder.add(this.light.position, 'z', -50, 50).listen().name('Z')
    lightFolder.add(this.light, 'intensity', -5, 50).listen().name('Intensity')

    if (!showControls) {
      this.gui.hide()
    }

    this.stopped = false
    this.speedStep = 0.025

    this.animate()
  }

  init () {
    // Renderer
    this.renderer = new THREE.WebGLRenderer({canvas: this.canvas, alpha: true})
    this.renderer.setPixelRatio(window.devicePixelRatio)
    this.renderer.setSize(this.canvas.clientWidth, this.canvas.clientHeight)

    // Camera
    this.camera = new THREE.PerspectiveCamera(60, this.canvas.clientWidth / this.canvas.clientHeight, 1, 1000)
    this.camera.position.set(22, 17, 0.15)

    this.controls = new THREE.OrbitControls(this.camera, this.canvas)

    // Scene
    this.scene = new THREE.Scene()

    // Light
    // this.light = new THREE.AmbientLight(0xFFF000)
    this.light = new THREE.PointLight(0xFFFFFF, 2.5, 100)
    this.light.position.set(50, 0, 50)
    this.scene.add(this.light)
    this.scene.add(new THREE.PointLightHelper(this.light, 5))

    // Pyramids
    this.ethTop = new THREE.Mesh(
      new THREE.ConeGeometry(5, 10, 4, 1, false),
      new THREE.MeshLambertMaterial({
        color: 0xff71ce,
        emissive: 0xff71ce,
        wireframe: false,
        opacity: 0.95,
        transparent: true
      })
    )
    this.ethTop.position.y = 4

    this.ethBottom = new THREE.Mesh(
      new THREE.ConeGeometry(5, 15, 4, 1, false),
      new THREE.MeshLambertMaterial({
        color: 0xff71ce,
        emissive: 0xff71ce,
        wireframe: false,
        opacity: 0.95,
        transparent: true
      })
    )
    this.ethBottom.rotation.x = Math.PI
    this.ethBottom.position.y = -10

    this.scene.add(this.ethTop)
    this.scene.add(this.ethBottom)
  }

  animate () {
    if (!this.stopped) { requestAnimationFrame(this.animate.bind(this)) }

    this.controls.update()
    this.ethBottom.rotation.y += this.speedStep
    this.ethTop.rotation.y -= this.speedStep
    this.renderer.render(this.scene, this.camera)
  }

  stop () {
    this.stopped = true
  }

  start () {
    this.stopped = false
    this.animate()
  }

  setSpeed (speed) {
    console.log('Setting speed...', speed)
    this.speedStep = speed
  }

}



