<template>
    <canvas
      @mouseover="logo.setSpeed(0.05)"
      @mouseleave="logo.setSpeed(0.025)"
      ref="canvas"
      id="eth-logo"></canvas>
</template>

<script>
  import EthLogo from '../js/eth-logo'

  export default {
    name: 'EthLogo',
    data () {
      return {
        logo: null
      }
    },
    mounted () {
      this.$nextTick(() => {
        this.logo = new EthLogo(this.$refs.canvas, false)
      })
    }
  }
</script>

<style scoped>
  #eth-logo{
    background: transparent;
    top: 0;
    left: 0;
    height: 20rem;
    width: 8rem;
    z-index: 1;
  }
</style>



