<template>
  <div :class="{active: state !== 'DISCONNECTED'}" class="index">
    <canvas class="bg-canvas" ref="canvas"></canvas>
    <transition
      mode="out-in"
      name="custom-classes-transition"
      enter-active-class="animated fadeIn"
      leave-active-class="animated fadeOut"
    >
      <div key="disonnected" class="disconnected" v-if="state === 'DISCONNECTED'">
        <img @click="shouldConnect = !shouldConnect"  src="https://66.media.tumblr.com/e781f53841d0faf3df45d9b40411fe34/tumblr_pa7x0s9JIk1whahvko1_640.gif">
      </div>
      <div key="hank" class="hank" v-else-if="state === 'READ_ONLY'">
        <eth-logo />
        <img src="../assets/hank.png">
        <eth-logo />
      </div>
      <div key="david" class="david" v-else>
        <eth-logo />
        <img src="../assets/head.gif">
        <eth-logo />
      </div>
    </transition>
    <web3 v-on:new-state="state = $event" v-if="shouldConnect" :bg-controller="bg"></web3>
  </div>
</template>

<script>
  import Web3 from './Web3.vue'
  import EthLogo from './EthLogo'
  import VaporWaveBackground from '../js/vaporwave-background'
  export default {
    name: 'index',
    components: {Web3, EthLogo},
    mounted () {
      this.bg = new VaporWaveBackground(this.$refs.canvas)
    },
    data () {
      return {
        bg: null,
        shouldConnect: false,
        state: 'DISCONNECTED'
      }
    },
    computed: {
    }
  }
</script>

<style lang="scss" type="text/scss">
  .index{
    background: #323231;
    transition: background-color 1s ease;
    &.active{
      background: #0B1234;
      canvas.bg-canvas{
        opacity: 1;
      }
    }
  }
  canvas.bg-canvas{
    position: absolute;
    top: 0;
    opacity: 0;
    background: #0B1234;
    transition: opacity 2s ease;
  }
  .david{
    img{
      max-width: 25rem;
      max-height: 25rem;
    }
  }
  .hank{
    position: absolute;
    img{
      height: 20rem;
    }
  }
  .hank, .david, .disconnected{
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    img{
      z-index: 9;
    }
  }
  .disconnected{
    img{
      border-radius: 2rem;
      cursor: pointer;
      transform: rotate(45deg);
      transition: all 1s ease;
      &:hover{
        transform: rotate(0deg);
      }
      height: 25rem;
    }
  }
  .dg .title{
    font-size: 1rem;
    margin: 0;
  }
</style>



